import AuthComponent from '../modules/portal/src/authenticated_component.js';
import SessionList from '../modules/portal/src/session_list.js';
import { Menu } from '../template/components.js';

import React from "react";

class MyForm extends React.Component {
  constructor(props) {
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.state = {
      status: ""
    };
  }

  render() {
    const { status } = this.state;
    return (
      <form
        onSubmit={this.submitForm}
        action="https://formspree.io/xwkervpa"
        method="POST"
      >
        <div className="form-group">
            <label htmlFor="email">Email:</label>
            <input className="form-control" type="email" name="email" />
        </div>
        <div className="form-group">
            <label htmlFor="message">Message:</label>
            <textarea name="message" className="form-control"></textarea>
        </div>
        <div class="form-group">
            <button type="submit" className="btn btn-dark btn-block">Submit</button>
        </div>
        {status === "SUCCESS" ? <p>Thanks!</p> : <button>Submit</button>}
        {status === "ERROR" && <p>Ooops! There was an error.</p>}
      </form>
    );
  }

  submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.setState({ status: "SUCCESS" });
      } else {
        this.setState({ status: "ERROR" });
      }
    };
    xhr.send(data);
  }
}

const Contact = () => (
    <Menu>
    <>
        <h3>Get in touch</h3>
        <div className="row" style={{"padding-top": "1em"}}>
            <div className="col col-sm-12 col-lg-6">
                <p>No matter what you have in mind, don't hesitate to get in touch.</p>
            </div>
            <div className="col col-sm-12 col-lg-6">
                <MyForm/>
            </div>
        </div>
    </>
    </Menu>
);
export default Contact;